		border-radius: 50%;
	}
	&:after {
		content: '';
		position: absolute;
		top: -110px;
		right: -180px;
		width: 420px;
		height: 420px;
		border: 21px solid rgba($white, 0.08);
		border-radius: 50%;
	}
}

.top-triangle {
	position: relative;
	z-index: 1;
	&:before {
		content: '';
