.overlay__grad {
	position: relative;
	z-index: 1;
	overflow: hidden;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(to right, var(--brand-primary), var(--secondary));
		opacity: 0.9;
		z-index: -1;
	}
}

.overlay__gradient {
	position: relative;
	z-index: 1;
	overflow: hidden;
	.overlay_after {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(to right, var(--brand-primary), var(--secondary));
		opacity: 0.94;
		z-index: -1;
		margin: 0;
	}
	&-left .overlay_after {
		background: linear-gradient(10deg, var(--brand-primary), var(--secondary));
	}
}

.overlay__white {
	position: relative;
	z-index: 1;
	overflow: hidden;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: $white;
		opacity: 0.94;
		z-index: -1;
	}
}

.overlay__dark {
	position: relative;
	z-index: 1;
	&:after {
		content: '';
