.arrow__pointer-top-left {
	display: none;
	position: absolute;
	top: -60px;
	right: -40px;

	[dir="rtl"] & {
		right: auto;
		left: -40px;
		transform: rotateY(180deg);
	}

	@media(min-width: 1200px) {
		display: block;
	}
}

.arrow__pointer-bottom-left {
	display: none;
	position: absolute;
	bottom: -60px;
	right: -40px;
	transform: rotateY(180deg) rotate(180deg);

	[dir="rtl"] & {
		right: auto;
		left: -40px;
		transform: rotateY(0) rotate(180deg);
	}

	@media(min-width: 1200px) {
		display: block;
	}
}

.arrow__pointer-top-right {
	display: none;
	position: absolute;
	top: -60px;
	left: -40px;
	transform: rotateY(180deg) rotate(0deg);

	[dir="rtl"] & {
		left: auto;
		right: -40px;
		transform: rotateY(0deg) rotate(0deg);
	}

	@media(min-width: 1200px) {
		display: block;
	}
}

.arrow__pointer-bottom-right {
	display: none;
	position: absolute;
	bottom: -60px;
	left: -40px;
	transform: rotateY(0deg) rotate(180deg);

	[dir="rtl"] & {
		left: auto;
		right: -40px;
		transform: rotateY(180deg) rotate(180deg);
	}

	@media(min-width: 1200px) {
		display: block;
	}
}

