.app-dot-line {
	display: inline-block;
	position: relative;
	z-index: 1;
	&.left {
		> .dot {
			right: -40px;

			[dir="rtl"] & {
				right: auto;
				left: -40px;
			}

			> .line {
				left: 0;

				[dir="rtl"] & {
					left: auto;
					right: 0;
				}
			}
		}
	}
	&.right {
		> .dot {
			left: -40px;

			[dir="rtl"] & {
				left: auto;
				right: -40px;
			}

			> .line {
				right: 0;

				[dir="rtl"] & {
					right: auto;
					left: 0;
				}
			}
		}
	}
	> .dot {
		display: none;
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
		width: 14px;
		height: 14px;
		border-radius: 50%;
		background: var(--brand-primary);
		@media(min-width: 1230px) {
			display: block;
		}
		&:after {
			content: '';
			position: absolute;
			top: -1px;
			left: -1px;
			right: -1px;
			bottom: -1px;
			border: 3px solid $white;
			box-shadow: 1px 1.732px 8px 0 rgba(0, 0, 0, 0.2);
			border-radius: 50%;
		}
		.line {
			position: absolute;
			top: 7px;
			width: 300px;
			height: 1px;
			background: var(--brand-primary);
			z-index: -1;
		}
	}
}

