.slide-bg-wrap {
	position: relative;
	overflow: hidden;
	.slide-bg {
		position: absolute;
		left: 0;
		display: block;
		width: 0;
		height: 0;
		border-radius: 50%;
		background-color: var(--brand-primary);
		transition: width 0.25s ease-in-out, height 0.25s ease-in-out;
		transform: translate(-50%, -50%);
		z-index: -1;
	}
	&:hover .slide-bg {
		width: 250%;
		height: 250%;
	}
}

