* + .subscr__form {
	margin-top: 30px;
}

.subscr__form {
	max-width: 520px;
	margin-left: auto;
	margin-right: auto;
	background: rgba($white, 0.12);
	border: 2px solid rgba($white, 0.2);
	border-radius: 50px;
	box-shadow: 0 3px 10px 0 rgba($black, 0.1);
	position: relative;
	.form-control {
		font-family: 'Open Sans', sans-serif;
		font-size: 16px;
		height: 52px;
		color: $white;
		padding: 10px 60px 10px 30px;
		background: transparent;
		border: none;

		@include placeholder($white);

		&:focus {
			box-shadow: none;
		}
	}
	.btn-subscr {
		position: absolute;
		width: 42px;
		height: 42px;
		border-radius: 50%;
		top: 50%;
		transform: translateY(-50%);
		right: 6px;
		color: $white;
		background: linear-gradient(to right, var(--brk-base-6), var(--brk-base-2));
		&:before {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		&:hover {
			transform: translateY(-50%) scale(1.1);
		}
	}
}

.video-wrap {
	position: relative;
	z-index: 1;
	cursor: pointer;
	box-shadow: 0 5px 16px 0 rgba($black, 0.4);
	min-height: 430px;
	.overlay-image {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
    background-position: center;
    background-repeat: no-repeat;
		background-size: cover;
		z-index: 50;
		-webkit-transition: all .4s ease;
		transition: all .4s ease;
		&.hid {
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
		}
	}
	.icon__btn {
		margin: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		.fa-play:before {
			margin-left: 2px;
		}
	}
	iframe {
		width: 100%;
		height: 100%;
	}
}

