/*
// Script styles
// ================================= */

#toTop {
	position: fixed;
	right: 20px;
	bottom: 20px;
	font-size: 0;
	width: 50px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	color: $white;
	background: var(--brand-primary);
	border-radius: 50%;
	overflow: hidden;
	text-decoration: none;
	z-index: 1200;
	visibility: visible;
	transform: scale(1);
	transition: \transform .3s ease;
	display: none;

	@media (min-width: 576px) {
		right: 23px;
		bottom: 23px;
	}

	&:before {
		font-family: "Font Awesome\ 5 Free";
		content: '\f106';
		font-weight: 900;
		font-size: 26px;
	}

	&:hover {
		color: $white;
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
		text-decoration: none;
	}

}

