// global rating
.brk-rating {
	position: relative;
	width: 89px;

	&__layer {
    @include clearfix;
    opacity: .6;

		i {
			display: inline-block;
			float: left;
			font-size: rem(14);
			margin: 0 1px;
		}

	}

	&__imposition {
		@include clearfix;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;

		.visible {
			width: 89px;
			height: 15px;
		}

		i {
			display: inline-block;
			float: left;
			font-size: rem(14);
			margin: 0 1px;
		}

	}

}

.center-absolute {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.brk-lightning-effect {
	position: relative;
	overflow: hidden;
	display: inline-block;

	&:before {
		content: '';
		width: 50px;
		position: absolute;
		top: 0;
		left: -50px;
		bottom: 0;
		z-index: 9;
		background: linear-gradient(to right, rgba(255, 255, 255, .2) 0%, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, .2) 100%);
	}

	&:hover {
		&:before {
			animation: lightning-effect 2s infinite ease-in-out;
		}
	}

	@keyframes lightning-effect {
		0% {
			left: -50px;
			background: linear-gradient(to right, rgba(255, 255, 255, .2) 0%, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, .2) 100%);
		}
		50% {
			left: 100%;
		}
		100% {
			left: -50px;
			background: transparent;
		}
	}
}

.brk-bg-center-cover {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;

	.container {
		z-index: 2;
