		}
		&_bottom-center {
			background-position: center bottom;
		}
		&_bottom-right {
			background-position: right bottom;
		}
		&_left-center {
			background-position: left center;
		}
		&_right-center {
			background-position: right center;
		}
		&_center {
			background-position: center center;
		}
	}
}


/* brk-subscribe */
.brk-subscribe {
	position: relative;

	[type="email"] {
		border-radius: 27px;
		padding: 0 33px;
		font-size: rem(14);
		border: 0;
	}

	button,
	[type="submit"] {
		position: absolute;
		top: 0;
		right: 0;
		width: 54px;
		height: 54px;
		border-radius: 50%;

		[dir="rtl"] & {
			right: auto;
			left: 0;
		}

		i {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: rem(16);
			font-weight: 400;
			vertical-align: 0;
		}
	}
}

.brk-subscribe-map {
	[type="email"] {
		background-color: var(--white);
		box-shadow: 0 5px 16px rgba(165, 165, 165, 0.5);
		color: var(--brk-base-3);
		@include placeholder(var(--brk-base-3));
		padding: 16px 33px;
		width: 100%;
	}

	button,
	[type="submit"] {
		background-color: var(--white);
		box-shadow: 0 5px 16px rgba(0,0,0,.3);

		i {
			color: var(--brand-primary);
		}
	}
}

.brk-subscribe-solid {
