		margin-top: 0;
		flex-grow: 1;
	}
	@media screen and (min-width: 992px) {
		.brk-form-strict {
			margin-left: 25px;
		}
	}
	button {
		margin-left: 0;
		padding-left: 55px;
		padding-right: 55px;

		@media screen and (min-width: 992px) {
			margin-left: 20px;
		}
		&:hover {
			background-color: rgba(256, 256, 256, .8);
		}
	}
	.brk-form-strict [type=text],
	.brk-form-strict [type=password],
