/* Default TABLE Style */
table {
	border-collapse: collapse;
	width: 100%;
	box-shadow: 0 6px 30px rgba(0,0,0,.12);

	@media (max-width: 767px) {
		box-shadow: none;
	}

	thead {
		box-shadow: 0 5px 10px rgba(0,0,0,.06), inset 0 -3px #fafafa;
		font-weight: bold;
		font-size: rem(16);
		line-height: 18px;
		position: relative;
		z-index: 9;

		@media (max-width: 767px) {
			display: none;
		}
	}

	th {
		padding-top: 25px;
		padding-bottom: 25px;
		position: relative;
	}

	tbody {
		font-weight: 300;
		font-size: rem(16);
		line-height: 18px;

		@media (max-width: 767px) {
			font-size: rem(14);
			line-height: 16px;
		}

		tr {
			border-bottom: 1px solid #eaeaea;

			@media (max-width: 767px) {
				display: block;
				margin-bottom: 15px;
				border: none;
				box-shadow: 0 6px 30px rgba(0,0,0,.12);
			}
		}
	}

	td {
		padding-top: 28px;
		padding-bottom: 28px;
		transition: all .3s ease-in-out;
	}

	th, td {
		text-align: center;
		padding-right: 15px;
		padding-left: 15px;

		@media (max-width: 767px) {
			text-align: right;
		}
	}
}
/* End Default TABLE Style */